import axiosInstance from './axiosConfig';

// 上传文献
export const uploadLiterature = async (formData: FormData) => {
  return axiosInstance.post('/literature/upload', formData, {
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  });
};

// 获取研究组文献列表
export const getGroupLiterature = async (groupId: string) => {
  return axiosInstance.get(`/literature/public/${groupId}`);
};

// 获取文献详情
export const getLiteratureDetail = async (literatureId: string) => {
  return axiosInstance.get(`/literature/detail/${literatureId}`);
};

// 预览文献
export const previewLiterature = async (literatureId: string) => {
  return axiosInstance.get(`/literature/view/file/${literatureId}`, {
    responseType: 'blob' // 处理二进制响应
  });
};

// 下载文献
export const downloadLiterature = async (literatureId: string) => {
  return axiosInstance.get(`/literature/download/${literatureId}`, {
    responseType: 'blob'
  });
};