import axiosInstance from './axiosConfig';

// AI问答
export const askAI = async (data: {
  question: string;
  literature_id: string;
  session_id?: string;
}) => {
  return axiosInstance.post('/ai/ask', data);
};

// 获取预设问题
export const getPresetQuestions = async (literatureId: string) => {
  return axiosInstance.get(`/ai/preset-questions/${literatureId}`);
};

// 获取用户会话列表
export const getUserSessions = async (params?: {
  literature_id?: string;
  limit?: number;
  offset?: number;
}) => {
  return axiosInstance.get('/ai/sessions', { params });
};

// 获取对话历史
export const getConversationHistory = async (sessionId: string, params?: {
  include_full_content?: boolean;
  limit?: number;
}) => {
  return axiosInstance.get(`/ai/conversation/${sessionId}`, { params });
};